.order-form__radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 7px;
}
.order-form__radio + label {
  position: relative;
  padding: 0 0 0 63px;
  cursor: pointer;
  margin-bottom: 25px;
}
.order-form__radio + label::before {
  content: " ";
  position: absolute;
  top: -9px;
  left: 0;
  width: 36px;
  height: 36px;
  border: 2px solid #444444;
  border-radius: 50%;
  background: #ffffff;
}
.order-form__radio:checked + label::after {
  content: " ";
  position: absolute;
  top: 3px;
  left: 12px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #63d1bb;
}
.order-form__radio:hover + label::before {
  border: 2px solid #63d1bb;
}
.order-form__radio:active + label::before {
  border: 2px solid #444444;
  opacity: 0.3;
}
.order-form__checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}
.order-form__checkbox + label {
  position: relative;
  padding: 0 0 0 60px;
  cursor: pointer;
  margin-bottom: 43px;
}
.order-form__checkbox + label::before {
  content: " ";
  position: absolute;
  top: -9px;
  left: 0;
  width: 36px;
  height: 36px;
  border: 2px solid #444444;
  background: #ffffff;
}
.order-form__checkbox:checked + label::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 6px;
  width: 40px;
  height: 30px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-size: 65%;
}
.order-form__checkbox:hover + label::before {
  border: 2px solid #63d1bb;
}
.order-form__checkbox:active + label::before {
  border: 2px solid #444444;
  opacity: 0.3;
}
.order-form__text {
  font-family: "Opensans", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  border: none;
  box-shadow: inset 0 -2px 0 0 #000000;
  padding-top: 18px;
  padding-bottom: 18px;
  margin-bottom: 24px;
  font-size: 17px;
  line-height: 18px;
  color: #444444;
  min-width: 260px;
  padding-left: 17px;
}
.order-form__text:hover {
  box-shadow: inset 0 -2px 0 0 #63d1bb;
}
.order-form__text:focus {
  outline: none;
  box-shadow: inset 0 -2px 0 0 #63d1bb;
}
::-webkit-input-placeholder {
  opacity: 0.3;
}
::-moz-placeholder {
  opacity: 0.3;
}
/* Firefox 19+ */
:-moz-placeholder {
  opacity: 0.3;
}
/* Firefox 18- */
:-ms-input-placeholder {
  opacity: 0.3;
}
.order-form__textarea {
  font-family: "Opensans", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  border: 2px solid #444444;
  min-width: 237px;
  min-height: 120px;
  margin-bottom: 18px;
  font-size: 17px;
  line-height: 18px;
  color: #444444;
  padding-left: 17px;
  padding-top: 18px;
  resize: none;
  overflow: auto;
}
.order-form__textarea:hover {
  border: 2px solid #63d1bb;
  outline: none;
}
.order-form__textarea:focus {
  border: 2px solid #63d1bb;
  opacity: 1.3;
  outline: none;
}
.order-form__explain {
  font-family: "Opensans", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  color: #444444;
  font-size: 14px;
  line-height: 16px;
}
.order-form__button {
  font-family: "Opensans-bold", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
  display: block;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  padding: 15px 40px;
  background-color: white;
  border: solid 2px #444444;
}
